import React, { useState, useEffect} from 'react';
import {BrowserRouter as Router, useHistory} from 'react-router-dom';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter,Form, FormGroup, Label, Input} from 'reactstrap';
import axios from 'axios';
import { apiUri } from '../../constants/defaultValues';

const Popups = (props) => {
  const {
    className, actionSet
  } = props;
   
  const history=useHistory();
  const handleHistory=()=>{
      history.push("/Multiple1");
  }
  
  const [hackData, SetChallengeList] = useState([])
  useEffect(() => {
  }, []);

  const [modal, setModal] = useState(false);
  const [hackName, setHackName] = useState(null);
  const [hackType, setHackType] = useState(null);

  const addHackathon = () => {
    const userId = JSON.parse(window.localStorage.getItem('sn_user'));
    console.log('user Id', userId)
    const data = {
      name: hackName,
      organizer_id: userId.id,
      hackathon_type_id: hackType
    }
    
    axios.post(apiUri+'/createHackathon', data).then((response) => {
      const dataItem = response.data;
      setModal(false)
      props.actionSet()
    });
  }
  const toggle = () => setModal(!modal);
  const hackTypeChange =(e) => {
    setHackType(e.target.value);
  }
  const onChangeHandler = (e) => {
    var filter = e.target.value;
    setHackName(filter)
  };
  console.log(hackType, hackName)
  return (
    <div>
           
      <Button color="primary" onClick={toggle}>Organize Hackathon</Button>
      <Modal isOpen={modal} toggle={toggle} className={className}>
        <ModalHeader toggle={toggle}>
            <h6>Great Decision</h6>
            <h1>Lets get you started</h1>
        </ModalHeader>
        <ModalBody>
        <Form>
      <FormGroup>
        <Label for="OrganizerName">Name</Label>
        <Input type="text" name="Name" id="organizerName" placeholder="Enter Your Name" onChange={(text) => onChangeHandler(text)} />
      </FormGroup>

      <FormGroup>
        <Label for="Hackathon">Hackathon</Label>
        {/* <Input type="select" name="Hackathon" id="Hackathon">
          <option>Online</option>
          <option>Offline</option>
         
        </Input> */}
        <select 
            value={hackType} 
            onChange={hackTypeChange} 
          style={{width: '100%', height: '30%'}}>
          <option value="0">Select Hackathon Type</option>
          <option value="1">Online</option>
            <option value="2">Blenders</option>
            <option value="3">Offline</option>
          </select>
          
      </FormGroup>
      </Form>
          </ModalBody>
        <ModalFooter>
            <Router>
          <Button  color="primary" onClick={addHackathon}>Begin</Button></Router>
          <Button color="secondary" onClick={toggle}>Cancel</Button>
        </ModalFooter>
      </Modal>
     
    </div>
  );
}

export default Popups;